@use "variables" as v;
@use "mixins" as m;

h1,
h2,
h3 {
	font-family: v.$fuenteSecundaria;
	font-weight: 700;
	margin: 0;
	line-height: 1.2;
}

h1 {
	font-size: 3.4rem;

	@include m.tablet {
		font-size: 4.4rem;
	}
}

h2 {
	font-size: 2.4rem;

	@include m.tablet {
		font-size: 3.4rem;
	}
}

h3 {
	font-size: 2.4rem;
}

a {
	text-decoration: none;
}

p {
	font-size: 1.6rem;
	line-height: 1.8;
}
