html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

[class*=__contenedor] {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

h1,
h2,
h3 {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  margin: 0;
  line-height: 1.2;
}

h1 {
  font-size: 3.4rem;
}
@media (min-width: 768px) {
  h1 {
    font-size: 4.4rem;
  }
}

h2 {
  font-size: 2.4rem;
}
@media (min-width: 768px) {
  h2 {
    font-size: 3.4rem;
  }
}

h3 {
  font-size: 2.4rem;
}

a {
  text-decoration: none;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
}

.header {
  padding: 5rem 0;
}
.header__barra {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10rem;
}
@media (min-width: 768px) {
  .header__barra {
    margin-bottom: 20rem;
  }
}
.header__logo {
  width: 15rem;
}
@media (min-width: 768px) {
  .header__logo {
    width: 24rem;
  }
}
.header__link {
  background-color: transparent;
  padding: 1rem 4rem;
  color: #ff52bf;
  margin-top: 2rem;
  display: inline-block;
  font-size: 1.5rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  margin-top: 0;
  border: 1px solid #ff52bf;
  font-size: 1.4rem;
  font-weight: 400;
  border-radius: 5rem;
  box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.25);
  padding: 0.6rem 2rem;
}
@media (min-width: 768px) {
  .header__link {
    width: auto;
    font-size: 2rem;
    padding: 2rem 5rem;
  }
}
.header__link:hover {
  background-color: rgba(26, 26, 26, 0);
  color: #ff85d2;
}
.header__link:hover {
  border: 1px solid #ff85d2;
}
@media (min-width: 768px) {
  .header__link {
    font-size: 1.8rem;
    padding: 1rem 4rem;
  }
}
.header__contenido {
  text-align: center;
  width: 90%;
  margin: 0 auto;
}
.header__texto {
  margin: 3rem auto 0 auto;
  font-size: 2rem;
}
@media (min-width: 1024px) {
  .header__texto {
    max-width: 50%;
  }
}
.header__boton {
  background-color: #ff52bf;
  padding: 1rem 4rem;
  color: #f5faff;
  margin-top: 2rem;
  display: inline-block;
  font-size: 1.5rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  border-radius: 5rem;
  margin-top: 4rem;
  box-shadow: 0px 2px 5px 1px rgba(0, 0, 0, 0.25);
}
@media (min-width: 768px) {
  .header__boton {
    width: auto;
    font-size: 2rem;
    padding: 2rem 5rem;
  }
}
.header__boton:hover {
  background-color: #ff85d2;
  color: white;
}

.hero {
  padding: 5rem 0;
}
.hero__imagen {
  margin: 0 auto 10rem auto;
  width: 90%;
  max-width: 80rem;
}
.hero__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10rem;
}
@media (min-width: 768px) {
  .hero__grid {
    flex-direction: row;
    justify-content: space-around;
  }
}

.estadistica__numero {
  font-size: 7rem;
  font-weight: 600;
  margin: -2rem 0;
}
@media (min-width: 768px) {
  .estadistica__numero {
    font-size: 9rem;
    margin: -3rem 0;
  }
}
@media (min-width: 1024px) {
  .estadistica__numero {
    font-size: 10rem;
  }
}
.estadistica__info {
  color: #a1a0a0;
  font-size: 2rem;
  margin: 0;
  text-align: center;
}
@media (min-width: 768px) {
  .estadistica__info {
    text-align: start;
  }
}

.section {
  position: relative;
  padding: 10rem 0;
}
.section--together {
  padding: 11rem 0 5rem 0;
}
@media (min-width: 768px) {
  .section--together {
    padding: 16rem 0 13rem 0;
  }
}
.section__wave1-top {
  position: absolute;
  left: 0;
  width: 100%;
  height: 11rem;
  background-image: url("../images/bg-section-top-mobile-1.svg");
  background-size: 100% 11rem;
  top: 0;
}
@media (min-width: 768px) {
  .section__wave1-top {
    position: absolute;
    left: 0;
    width: 100%;
    height: 16rem;
    background-image: url("../images/bg-section-top-desktop-1.svg");
    background-size: 100% 16rem;
    top: 0;
  }
}
.section__wave1-bottom {
  position: absolute;
  left: 0;
  width: 100%;
  height: 5rem;
  background-image: url("../images/bg-section-bottom-mobile-1.svg");
  background-size: 100% 5rem;
  bottom: 0;
}
@media (min-width: 768px) {
  .section__wave1-bottom {
    position: absolute;
    left: 0;
    width: 100%;
    height: 13rem;
    background-image: url("../images/bg-section-bottom-desktop-1.svg");
    background-size: 100% 13rem;
    bottom: 0;
  }
}
.section--users {
  padding: 10rem 0;
}
@media (min-width: 768px) {
  .section--users {
    padding: 15rem 0 13rem 0;
  }
}
.section__wave2-top {
  position: absolute;
  left: 0;
  width: 100%;
  height: 10rem;
  background-image: url("../images/bg-section-top-mobile-2.svg");
  background-size: 100% 10rem;
  top: 0;
}
@media (min-width: 768px) {
  .section__wave2-top {
    position: absolute;
    left: 0;
    width: 100%;
    height: 15rem;
    background-image: url("../images/bg-section-top-desktop-2.svg");
    background-size: 100% 15rem;
    top: 0;
  }
}
.section__wave2-bottom {
  position: absolute;
  left: 0;
  width: 100%;
  height: 10rem;
  background-image: url("../images/bg-section-bottom-mobile-2.svg");
  background-size: 100% 10rem;
  bottom: 0;
}
@media (min-width: 768px) {
  .section__wave2-bottom {
    position: absolute;
    left: 0;
    width: 100%;
    height: 13rem;
    background-image: url("../images/bg-section-bottom-desktop-2.svg");
    background-size: 100% 13rem;
    bottom: 0;
  }
}
.section__contenido {
  background-color: #f5faff;
  padding: 5rem 0;
}
.section__contenido--bgwhite {
  background-color: unset;
}
.section__grid {
  display: flex;
  flex-direction: column;
  gap: 10rem;
}
@media (min-width: 768px) {
  .section__grid {
    display: grid;
    gap: 15rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    grid-auto-flow: column;
  }
}
@media (min-width: 768px) {
  .section__imagen {
    grid-column: 2/3;
  }
  .section__imagen--left {
    grid-column: unset;
  }
}
.section__textos {
  text-align: center;
}
@media (min-width: 768px) {
  .section__textos {
    text-align: left;
  }
}
.section__descripcion {
  line-height: 1.6;
}

.community {
  padding: 15rem 0;
  text-align: center;
}
.community__boton {
  background-color: #ff52bf;
  padding: 1rem 4rem;
  color: #f5faff;
  margin-top: 2rem;
  display: inline-block;
  font-size: 1.5rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  border-radius: 5rem;
  margin-top: 4rem;
  box-shadow: 0px 2px 5px 1px rgba(0, 0, 0, 0.25);
}
@media (min-width: 768px) {
  .community__boton {
    width: auto;
    font-size: 2rem;
    padding: 2rem 5rem;
  }
}
.community__boton:hover {
  background-color: #ff85d2;
  color: white;
}

.footer {
  position: relative;
  margin-top: 5rem;
  padding-top: 5rem;
}
@media (min-width: 768px) {
  .footer {
    padding-top: 15rem;
  }
}
.footer__wave {
  position: absolute;
  left: 0;
  width: 100%;
  height: 5rem;
  background-image: url("../images/bg-footer-top-mobile.svg");
  background-size: 100% 5rem;
  top: 0;
}
@media (min-width: 768px) {
  .footer__wave {
    position: absolute;
    left: 0;
    width: 100%;
    height: 15rem;
    background-image: url("../images/bg-footer-top-desktop.svg");
    background-size: 100% 15rem;
    top: 0;
  }
}
.footer__contenido {
  background-color: #00252e;
  color: #f5faff;
  padding: 7rem 0;
}
.footer__grid {
  display: grid;
  gap: 10rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 10rem;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-flow: column;
  }
}
@media (min-width: 1024px) {
  .footer__grid {
    gap: 20rem;
  }
}
@media (min-width: 768px) {
  .footer__widget:first-child {
    grid-column: 2/3;
  }
}
.footer__heading {
  font-size: 3rem;
  font-weight: 700;
  text-transform: uppercase;
  margin: 0;
}
@media (min-width: 768px) {
  .footer__texto {
    max-width: 90%;
  }
}
@media (min-width: 1024px) {
  .footer__texto {
    max-width: 60%;
  }
}
.footer__contacto {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}
.footer__telefono, .footer__correo {
  margin: 0.5rem 0;
}

.formulario {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  position: relative;
}
@media (min-width: 1024px) {
  .formulario {
    flex-direction: row;
    align-items: center;
    gap: 1rem;
    max-width: 85%;
  }
}
.formulario__input {
  padding: 1.1rem 1rem;
  width: 100%;
  border-radius: 0.5rem;
  border: none;
  outline: none;
  transition: display 0.3s ease;
}
.formulario__input::placeholder {
  opacity: 0;
}
@media (min-width: 768px) {
  .formulario__input {
    padding: 1.2rem 1rem;
  }
  .formulario__input:hover + .formulario__span, .formulario__input:focus-visible + .formulario__span {
    display: block;
  }
  .formulario__input:hover, .formulario__input:focus-visible {
    outline: 2px solid #ff7575;
  }
  .formulario__input:hover::placeholder {
    opacity: 0.5;
  }
}
.formulario__span {
  display: none;
}
@media (min-width: 768px) {
  .formulario__span {
    position: absolute;
    bottom: 3rem;
    left: 0;
    font-size: 1.2rem;
    color: #ff7575;
  }
}
@media (min-width: 1024px) {
  .formulario__span {
    bottom: -2rem;
  }
}
.formulario__submit {
  background-color: #ff52bf;
  padding: 1rem 4rem;
  color: #f5faff;
  margin-top: 2rem;
  display: inline-block;
  font-size: 1.5rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  margin-top: 1rem;
  border-radius: 0.5rem;
  max-width: 20rem;
  padding: 1.2rem 4rem;
}
@media (min-width: 768px) {
  .formulario__submit {
    width: auto;
    font-size: 2rem;
    padding: 2rem 5rem;
  }
}
.formulario__submit:hover {
  background-color: #ff85d2;
  color: white;
}
@media (min-width: 768px) {
  .formulario__submit {
    padding: 1rem 2rem;
    max-width: 100%;
  }
}
@media (min-width: 1024px) {
  .formulario__submit {
    margin-top: 0;
  }
}

.sociales {
  margin-top: 4rem;
  display: flex;
  gap: 2rem;
}
.sociales__icono {
  font-size: 3.5rem;
}
.sociales__icono:hover {
  cursor: pointer;
  color: #2ed6ff;
}